<template>
  <div class="destail">
    <Form :label-width="60" class="write-form" style="border:0">
      <div class="model_box sec_list" v-for="(items,index) in destailConfig" :key="index" @mouseover="mouseover(index)">
        <Icon
          type="ios-remove-circle-outline"
          class="delSel form_btn_remove"
          size="24"
          v-if="destailConfig.length!=1"
          @click="colRemove(index)"
        />
        <!-- <span class="num">{{index+1}}</span> -->
         <FormItem label="排序" style="width:100px">
          <InputNumber
            v-model="items.sort"
            size="small"
            :min="1"
            :max="100"
            @on-blur="changeSort(items.sort,index)"
          ></InputNumber>
        </FormItem>
        <FormItem label="列标题">
          <Input v-model="items.headerName" size="small" style="width:100px" clearable/>
        </FormItem>
        <FormItem label="字段名">
          <Input v-model="items.key" class="form_zd" size="small" style="width:100px" readonly/>
        </FormItem>
        <FormItem label="栅格">
          <Select v-model="items.col" size="small" style="width:60px" clearable>
            <Option :value="12">12</Option>
            <Option :value="24">24</Option>
          </Select>
          <!-- <InputNumber v-model="items.destailListCol" size="small" style="width:60px !important" clearable></InputNumber> -->
        </FormItem>
        <!-- {{items}} -->
      
        <!-- <Button type="primary" size="small" @click="colRemove(index)">-</Button> -->
        <!-- <Button type="primary" size="small" @click="colAdd(index)">+</Button> -->
        <p class="clear"></p>
      </div>
    </Form>
  </div>
</template>
<script>
// import config from "@/../static/config.json";
// config.forEach((item, index) => {
//   item.destailListCol = 12;
// });
// console.log(config);
import { deepCopy } from "@/util/util.js";
export default {
  data() {
    return {
      destailConfig: []
    };
  },
  methods: {
    //详情添加列表项事件
    colRemove(index) {
      this.$store.commit("changeColums", {
        key: this.destailConfig[index].key,
        setName: "isDetails"
      });
      this.destailConfig.splice(index, 1);
    },
    //获取数据事件
    getDestailConfig() {
      //   console.log(1);
      let data = deepCopy(this.$store.state.colums, []);
      let keys = "";
      this.destailConfig.forEach(item => {
        keys += item.key;
      });
      data.forEach(item => {
          // console.log(item.isDetails)
        if (item.isDetails) {
          if (keys.indexOf(item.key) < 0) {
            item.col = 12;
            item.sort = null;
            this.destailConfig.push(item);
          }
        }else{
           this.destailConfig.forEach((items,index)=>{
             if(items.key==item.key){
                this.destailConfig.splice(index,1)
             }
           })
        }
      });
       this.getSort();
    },
      //排序写入
    getSort() {
      this.destailConfig.forEach((item, index) => {
        item.sort = index + 1;
      });
    },
    //重新排序
    changeSort(num, index) {
      let item = this.destailConfig[index];
      this.destailConfig.splice(index, 1);
      if (num > this.destailConfig.length + 1) {
        num = this.destailConfig.length + 1;
      } 
       this.destailConfig.splice(num - 1, 0, item);
      // if (num - 1 < index) {
       
      // } else {
      //   this.destailConfig.splice(num-1, 0, item);
      // }
      this.getSort();
       $(".destail .sec_list").removeClass("add");
       $(".destail .sec_list").eq(num-1).addClass("add");
    },
   //鼠标移入事件
    mouseover(index){
      // console.log(1)
       $(".destail .sec_list").removeClass("addClass");
       $(".destail .sec_list").eq(index).addClass("addClass");
    },
     //生产代码时数据处理
    processingData(){
       let data=[];
       if(this.destailConfig.length>0){
         this.destailConfig.forEach(item=>{
           let obj={
              headerName: item.headerName,
              field: item.key,
              col:item.col
           }
           data.push(obj)
         })
        this.$store.commit("changeDestailCllums", data);
       }
    }
  }
};
</script>
<style scoped>
.model_box {
  padding: 5px 0;
  border-bottom: 1px dashed #333333;
}
/* .destail{
  width: 578px;
  margin: 0 auto;
} */

</style>



