<template>
  <div>
    <div class="filedBtn">
      <Button type="primary" size="small" @click="textareaModal=true">输入json字符串</Button>
      <Button type="primary" size="small" @click="fileModal=true">上传json文件</Button>
      <Button type="primary" size="small" @click="interfaceModal=true">通过接口读取字段配置</Button>
    </div>
    <!--  -->
    <Tabs class="card-main" value="filedMain" @on-click="tabsbtn">
      <TabPane label="字段配置" name="filedMain">
        <div class="table-head">
          <div class="cell" style="width:40px">序号</div>
          <div class="cell" style="width:40px">主键</div>
          <div class="cell" style="width:150px">标签</div>
          <div class="cell" style="width:150px">字段名</div>
          <div class="cell" style="width:280px">
            <Checkbox v-model="isSearch" @on-change="checkAllSearch">查询</Checkbox>
            <Checkbox v-model="isTable" @on-change="checkAllTable">列表</Checkbox>
            <Checkbox v-model="isDetails" @on-change="checkAllDetails">详情</Checkbox>
            <Checkbox
              v-model="isForm"
              @on-change="checkAllForm"
              v-if="$store.state.chooseModel.hasAdd"
            >表单</Checkbox>
            <Checkbox
              v-model="isExport"
              @on-change="checkAllExport"
              v-if="$store.state.chooseModel.hasExport"
            >导出</Checkbox>
          </div>
          <p class="clear"></p>
        </div>
        <!-- <Row class="table-head">
          <Col span="1">
            <div class="cell">序号</div>
          </Col>
          <Col span="2">
            <div class="cell">主键</div>
          </Col>
          <Col span="5">
            <div class="cell">标签</div>
          </Col>
          <Col span="5">
            <div class="cell">字段名</div>
          </Col>
          <!-- <Col span="5">
				<div class="cell">控件类型</div>
            </Col>
        -->
        <!-- <Col span="6">
            <div class="cell">
              <Checkbox v-model="isSearch" @on-change="checkAllSearch">查询</Checkbox>
              <Checkbox v-model="isTable" @on-change="checkAllTable">列表</Checkbox>
              <Checkbox v-model="isDetails" @on-change="checkAllDetails">详情</Checkbox>
              <Checkbox v-model="isForm" @on-change="checkAllForm"  v-if="$store.state.chooseModel.hasAdd">表单</Checkbox>
              <Checkbox v-model="isExport" @on-change="checkAllExport" v-if="$store.state.chooseModel.hasExport">导出</Checkbox>
            </div>
          </Col>
        </Row>-->
        <div v-for="(items,index) in filedInfo" :key="index" class="table-body">
          <div class="cell" style="width:40px">{{index+1}}</div>
          <div class="cell" style="width:40px" @click="setKey(index,false)" v-if="items.isKey">
            <Icon type="md-checkmark" />
          </div>
          <div class="cell" style="width:40px" @click="setKey(index,true)" v-else></div>
          <div class="cell" style="width:150px">{{items.headerName}}</div>
          <div class="cell" style="width:150px">{{items.key}}</div>
          <div class="cell" style="width:280px">
            <Checkbox v-model="items.isSearch" @on-change="checkOneSearch">查询</Checkbox>
            <Checkbox v-model="items.isTable" @on-change="checkOneTable">列表</Checkbox>
            <Checkbox v-model="items.isDetails" @on-change="checkOneDetails">详情</Checkbox>
            <Checkbox
              v-model="items.isForm"
              @on-change="checkOneForm"
              v-if="$store.state.chooseModel.hasAdd"
            >表单</Checkbox>
            <Checkbox
              v-model="items.isExport"
              @on-change="checkOneExport"
              v-if="$store.state.chooseModel.hasExport"
            >导出</Checkbox>
          </div>
          <p class="clear"></p>
        </div>
        <!-- <Row v-for="(items,index) in filedInfo" :key="index" class="table-body">
          <Col span="1">
            <div class="cell">{{index}}</div>
          </Col>
          <Col span="2" style="padding: 0px;">
            <div class="cell" @click="setKey(index,false)" v-if="items.isKey">
              <Icon type="md-checkmark" />
            </div>
            <div class="cell" @click="setKey(index,true)" v-else></div>
          </Col>
          <Col span="5">
            <div class="cell">{{items.headerName}}</div>
          </Col>
          <Col span="5">
            <div class="cell">{{items.key}}</div>
          </Col>
          <!-- <Col span="5">
						<div class="cell">
							<Select v-model="items.type">
						        <Option v-for="(typeItem,typeIndex) in typeList" :value="typeItem.value" :key="typeIndex">{{typeItem.label}}</Option>
						    </Select>
						</div>
        </Col>-->
        <!-- <Col span="6">
            <div class="cell">
              <Checkbox v-model="items.isSearch" @on-change="checkOneSearch">查询</Checkbox>
              <Checkbox v-model="items.isTable" @on-change="checkOneTable">列表</Checkbox>
              <Checkbox v-model="items.isDetails" @on-change="checkOneDetails">详情</Checkbox>
              <Checkbox
                v-model="items.isForm"
                @on-change="checkOneForm"
                v-if="$store.state.chooseModel.hasAdd"
              >表单</Checkbox>
              <Checkbox
                v-model="items.isExport"
                @on-change="checkOneExport"
                v-if="$store.state.chooseModel.hasExport"
              >导出</Checkbox>
            </div>
        </Col>-->
        <!-- </Row>  -->
      </TabPane>
      <TabPane label="查询配置" name="name2">
        <queryConfig ref="query"></queryConfig>
      </TabPane>
      <TabPane label="列表配置" name="tableListConfig">
        <tableConfig ref="tableListConfig"></tableConfig>
      </TabPane>
      <TabPane label="详情配置" name="destailConfig">
        <destailConfig ref="destailConfig"></destailConfig>
      </TabPane>
      <TabPane label="表单配置" name="fromConfig" v-if="$store.state.chooseModel.hasAdd">
        <fromConfig ref="fromConfig"></fromConfig>
      </TabPane>
      <TabPane label="导出配置" name="exportConfig" v-if="$store.state.chooseModel.hasExport">
        <exportConfig ref="exportConfig"></exportConfig>
      </TabPane>
    </Tabs>
    <Modal
      v-model="textareaModal"
      @on-ok="readJson"
      title="输入json字符串(必须为json格式数组)"
      :closable="false"
      :mask-closable="false"
    >
      <Input v-model="filedJson" type="textarea" :rows="4" placeholder="输入json字符串" />
    </Modal>
    <Modal
      v-model="fileModal"
      @on-ok="fileJson"
      title="上传json文件"
      :closable="false"
      :mask-closable="false"
    >
      <input type="file" id="fileId"  />
    </Modal>
    <Modal
      v-model="interfaceModal"
      @on-ok="fileJson"
      title="通过接口读取字段配置"
      :closable="false"
      :mask-closable="false"
    >
      <Input v-model="interfaceUrl" placeholder="请输入读取字段配置的接口" clearable />
    </Modal>
  </div>
</template>

<script>
import config from "@/../static/config.json";
import fromConfig from "./children/fromConfig";
import queryConfig from "./children/queryConfig";
import tableConfig from "./children/tableConfig";
import destailConfig from "./children/destailConfig";
import exportConfig from "./children/exportConfig";
import { deepCopy } from "@/util/util.js";
let _this;
export default {
  name: "filedConfig",
  components: {
    queryConfig,
    tableConfig,
    destailConfig,
    fromConfig,
    exportConfig
  },
  data() {
    return {
      filedInfo: [],
      typeList: [
        { value: "input", label: "文本输入框" },
        { value: "select", label: "下拉选择框" },
        { value: "ipt_number", label: "数字搜索框" }
      ],
      textareaModal: false,
      fileModal: false,
      interfaceModal: false,
      filedJson: "",
      interfaceUrl: "",
      isSearch: false,
      isTable: false,
      isDetails: false,
      isForm: false,
      isExport: false
    };
  },
  mounted() {
    // this.csDataExport();
    _this = this;
  },
  methods: {
    setKey(index, v) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isKey", false);
      });
      this.$set(this.filedInfo[index], "isKey", v);
    },
    //输入配置json事件
    readJson() {
      this.filedInfo = JSON.parse(this.filedJson);
      //  console.log(data)
      this.exportDataConfig();
    },
    //测试初始数据导入
    csDataExport() {
      this.filedInfo = deepCopy(config, []);
      this.exportDataConfig();
    },
    //导入数据配置事件
    exportDataConfig() {
      this.filedInfo.forEach(function(item, index) {
        item.isTable = true;
        item.isDetails = true;
        item.isForm = true;
        item.isExport = true;
        item.isSearch = false;
        item.type = "input";
      });
       this.filedInfo[0].isKey=true
      this.isTable = true;
      this.isDetails = true;
      this.isForm = true;
      this.isExport = true;
      this.$store.commit("setColums", this.filedInfo);
    },
    //上传导入文件
    fileJson() {
      var objFile = document.getElementById("fileId");
      if (objFile.value == "") {
        this.$message.error("文件数据不能为空");
      }
      //   console.log(objFile.files[0]); // 文件信息
      var files = $("#fileId").prop("files"); //获取到文件列表
      if (files.length == 0) {
        this.$message.error("请先选择文件");
      } else {
        var reader = new FileReader(); //新建一个FileReader
        reader.readAsText(files[0], "UTF-8"); //读取文件
        reader.onload = function(evt) {
          //读取完文件之后会回来这里
          var fileString = evt.target.result; // 读取文件内容
          //    console.log(fileString);
          _this.filedInfo = JSON.parse(fileString);
          _this.exportDataConfig();
        };
      }
    },
    // getFile(e) {

    // },
    checkAllSearch(isCheck) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isSearch", isCheck);
      });
    },
    checkAllTable(isCheck) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isTable", isCheck);
      });
    },
    checkAllDetails(isCheck) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isDetails", isCheck);
      });
    },
    checkAllForm(isCheck) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isForm", isCheck);
      });
    },
    checkAllExport(isCheck) {
      this.filedInfo.forEach((fc_item, fc_index) => {
        this.$set(this.filedInfo[fc_index], "isExport", isCheck);
      });
    },
    checkOneSearch(isCheck) {
      if (isCheck) {
        let isAllCheck = true;
        for (let i = 0; i < this.filedInfo.length; i++) {
          if (!this.filedInfo[i].isSearch) {
            isAllCheck = false;
            break;
          }
        }
        this.isSearch = isAllCheck;
      } else {
        this.isSearch = false;
      }
    },
    checkOneTable(isCheck) {
      if (isCheck) {
        let isAllCheck = true;
        for (let i = 0; i < this.filedInfo.length; i++) {
          if (!this.filedInfo[i].isTable) {
            isAllCheck = false;
            break;
          }
        }
        this.isTable = isAllCheck;
      } else {
        this.isTable = false;
      }
    },
    checkOneDetails(isCheck) {
      if (isCheck) {
        let isAllCheck = true;
        for (let i = 0; i < this.filedInfo.length; i++) {
          if (!this.filedInfo[i].isDetails) {
            isAllCheck = false;
            break;
          }
        }
        this.isDetails = isAllCheck;
      } else {
        this.isDetails = false;
      }
    },
    checkOneForm(isCheck) {
      if (isCheck) {
        let isAllCheck = true;
        for (let i = 0; i < this.filedInfo.length; i++) {
          if (!this.filedInfo[i].isForm) {
            isAllCheck = false;
            break;
          }
        }
        this.isForm = isAllCheck;
      } else {
        this.isForm = false;
      }
    },
    checkOneExport(isCheck) {
      if (isCheck) {
        let isAllCheck = true;
        for (let i = 0; i < this.filedInfo.length; i++) {
          if (!this.filedInfo[i].isExport) {
            isAllCheck = false;
            break;
          }
        }
        this.isExport = isAllCheck;
      } else {
        this.isExport = false;
      }
    },
    // tabs点击事件
    tabsbtn(name) {
      this.$store.commit("setColums", this.filedInfo);
      if (name == "name2") {
        this.$refs["query"].getQueryClums();
      }
      if (name == "fromConfig") {
        this.$refs["fromConfig"].setFromModelData();
      }
      if (name == "tableListConfig") {
        this.$refs["tableListConfig"].getTableColumnsAll();
      }
      if (name == "destailConfig") {
        this.$refs["destailConfig"].getDestailConfig();
      }
      if (name == "exportConfig") {
        this.$refs["exportConfig"].getExcelClumns();
      }
      if (name == "filedMain") {
        let isSearch = true;
        let isTable = true;
        let isDetails = true;
        let isForm = true;
        let isExport = true;
        this.filedInfo = deepCopy(this.$store.state.colums, []);
        this.filedInfo.forEach(function(item, index) {
          if (!item.isSearch) {
            isSearch = false;
          }
          if (!item.isTable) {
            isTable = false;
          }
          if (!item.isSearch) {
            isSearch = false;
          }
          if (!item.isDetails) {
            isDetails = false;
          }
          if (!item.isForm) {
            isForm = false;
          }
          if (!item.isExport) {
            isExport = false;
          }
        });
        this.isSearch = isSearch;
        this.isTable = isTable;
        this.isDetails = isDetails;
        this.isForm = isForm;
        this.isExport = isExport;
      }
    },
    // 当前页面导入数据后各个数据配置事件
    dataChange() {
     
      let queryCllums = [],
        destailCllums = [],
        exportCllums = [],
        fromColums = [],
        tableCllums = [];
      if (this.filedInfo.length > 0) {
        this.filedInfo.forEach(item => {
          if (item.isSearch) {
            queryCllums.push({
              type: "input",
              fromType: "text",
              placeholder: item.headerName, //提示信息
              key: item.key //传入键 key
            });
          }
          if (item.isTable) {
            tableCllums.push({
              headerName: item.headerName,
              field: item.key,
              width: 150,
              cellClass: "col_center",
              resizable: true
            });
          }
          if (item.isDetails) {
            destailCllums.push({
              headerName: item.headerName,
              field: item.key,
              col: 12
            });
          }
          if (item.isExport) {
            exportCllums.push({
              headerName: item.headerName,
              field: item.key
            });
            if (item.isForm) {
              fromColums.push({
                type: "input",
                label: item.headerName,
                key: item.key, //传入键 key
                message: item.headerName + "不能为空"
              });
            }
            if(item.isKey){
               this.$store.state.chooseModel.mainKey=item.key
            }
          }
        });
        this.$store.commit("changeQueryCllums", queryCllums);
        this.$store.commit("changeTableCllums", tableCllums);
        this.$store.commit("changeDestailCllums", destailCllums);
        this.$store.commit("changeExportCllums", exportCllums);
        this.$store.commit("changeFromColums", fromColums);  
        this.$refs["query"].processingData();
        // this.$refs["fromConfig"].setFromModelData();
        this.$refs["tableListConfig"].processingData();
        this.$refs["destailConfig"].processingData();
        this.$refs["exportConfig"].processingData();
      }
    }
  }
};
</script>
<style>
.filedBtn {
  padding: 5px;
  margin-top: 15px;
}

.card-main .ivu-tabs-tabpane {
  /* overflow: auto; */
}

.table-head,
.table-body {
  border: 1px solid #e8eaec;
  width: 100%;
  margin: 0 auto;
}

.table-head .cell {
  text-align: center;
  border-right: 1px solid #e8eaec;
  background: #f8f8f9;
  float: left;
  /*padding-top: 5px;
		padding-bottom: 5px;*/
}

.table-body .cell {
  text-align: center;
  border-right: 1px solid #e8eaec;
  float: left;
}

.table-body .cell,
.table-head .cell {
  height: 30px;
  line-height: 30px;
  text-align: center;
}

.table-body:hover {
  background-color: #ebf7ff;
}
.ivu-icon-md-checkmark{
  color: red;
  font-size: 16px;
}
</style>


