<template>
  <div class="export">
    <Form :label-width="65" class="write-form " style="border:0">
      <div class="model_box sec_list" v-for="(items,index) in excelClumns" :key="index"  @mouseover="mouseover(index)">
        <Icon
          type="ios-remove-circle-outline"
          class="delSel"
          size="24"
          v-if="excelClumns.length!=1"
          @click="colRemove(index)"
        />
        <!-- <span class="num">{{index+1}}</span> -->
        <FormItem label="排序" style="width:100px">
          <InputNumber
            v-model="items.sort"
            size="small"
            :min="1"
            :max="100"
            @on-blur="changeSort(items.sort,index)"
          ></InputNumber>
        </FormItem>
        <FormItem label="列标题">
          <Input v-model="items.headerName" size="small" style="width:100px" clearable />
        </FormItem>
        <FormItem label="字段名">
          <Input v-model="items.key" size="small" class="form_zd" style="width:100px" readonly />
        </FormItem>
        <!-- <Button type="primary" size="small" @click="colAdd(index)">+</Button> -->
        <p class="clear"></p>
      </div>
    </Form>
  </div>
</template>
<script>
// import config from "@/../static/config.json";
// config.forEach((item, index) => {
//   item.destailListCol = 12;
// });
import { deepCopy } from "@/util/util.js";
export default {
  data() {
    return {
      excelClumns: []
    };
  },
  methods: {
    //列表删除事件
    colRemove(index) {
      this.$store.commit("changeColums", {
        key: this.excelClumns[index].key,
        setName: "isExport"
      });
      this.excelClumns.splice(index, 1);
    },
    getExcelClumns() {
      let data = deepCopy(this.$store.state.colums, []);
      let keys = "";
      this.excelClumns.forEach(item => {
        keys += item.key;
      });
      data.forEach(item => {
        if (item.isExport) {
          if (keys.indexOf(item.key) < 0) {
             item.sort = null;
            this.excelClumns.push(item);
          }
        }else{
           this.excelClumns.forEach((items,index)=>{
             if(items.key==item.key){
                this.excelClumns.splice(index,1)
             }
           })
        }
      });
      this.getSort();
    },
      //排序写入
    getSort() {
      this.excelClumns.forEach((item, index) => {
        item.sort = index + 1;
      });
    },
    //重新排序
    changeSort(num, index) {
      let item = this.excelClumns[index];
      this.excelClumns.splice(index, 1);
      if (num > this.excelClumns.length + 1) {
        num = this.excelClumns.length + 1;
      } 
      this.excelClumns.splice(num - 1, 0, item);
      this.getSort();
       console.log(1)
       $(".export .sec_list").removeClass("add");
       $(".export .sec_list").eq(num-1).addClass("add");
    },
   //鼠标移入事件
    mouseover(index){
     
       $(".export .sec_list").removeClass("addClass");
       $(".export .sec_list").eq(index).addClass("addClass");
    },
     //生产代码时数据处理
    processingData(){
       let data=[];
       if(this.excelClumns.length>0){
         this.excelClumns.forEach(item=>{
           let obj={
             headerName: item.headerName,
             field: item.key
           }
           data.push(obj)
         })
        this.$store.commit("changeExportCllums", data);
       }
    }
  }
};
</script>
<style scoped>
.model_box {
  padding: 5px 0;
  border-bottom: 1px dashed #333333;
}
/* .export{
  width: 470px;
  margin: 0 auto;
} */
</style>



