<template>
	<div class="formset">
		<div class="where_ipt model_box" style="padding-top: 5px;">
			<Form v-for="(items,index) in fromModelData" :key="index" :model="items" :label-width="98" class="write-form del-form form-list">
				<Icon type="ios-remove-circle-outline" size="24" @click="delFromBtn(index)" class="form-del" />
				<div class="model_box">
					<FormItem label="排序" :label-width="68">
						<Input v-model="items.sort" size="small" style="width:30px" @on-blur="changeSort(items.key)" />
					</FormItem>
					<FormItem label="字段" :label-width="50">
						<Tag color="blue" type="border">{{items.key}}</Tag>
					</FormItem>
					<FormItem label="标题" :label-width="58">
						<Input v-model="items.label" size="small" placeholder="标题" style="width:100px"/>
					</FormItem>
					<FormItem label="宽度比例" :label-width="65">
						<Select v-model="items.col" size="small" style="width:80px">
							<Option value="24">整行</Option>
							<Option value="12">一半</Option>
						</Select>
					</FormItem>
					<FormItem label="表单类型" :label-width="68">
						<Select v-model="items.type" size="small" placeholder="请选择表单类型" @on-change="whereChange(index)" style="width:100px">
							<Option value="input">文本输入框</Option>
							<Option value="radio">单选框</Option>
							<Option value="checkbox">复选框</Option>
							<Option value="switch">开关</Option>
							<Option value="select">下拉选择框</Option>
							<Option value="autoComplete">自动完成</Option>
							<Option value="data">日期选择器</Option>
							<Option value="cascader">级联选择</Option>
							<Option value="ipt_number">数字搜索框</Option>
							<Option value="rate">评分</Option>
							<Option value="search">搜索（点击弹出框）</Option>
						</Select>
					</FormItem>
					<FormItem label="类型" v-if="items.type=='input'" :label-width="50">
						<Select v-model="items.fromType" size="small" style="width:90px">
							<Option value="text">文本框</Option>
							<Option value="textarea">文本域</Option>
							<Option value="password">密码框</Option>
						</Select>
					</FormItem>
					<!-- 多行文本域 -->
					<FormItem label="文本域行数" v-if="items.fromType=='textarea'">
						<Input type="number" v-model="items.rows" size="small" placeholder="文本域高度" style="width:100px" clearable />
					</FormItem>
					<!-- 数字框 -->
					<FormItem label="小数位数" v-if="items.type=='ipt_number'">
						<Input type="number" v-model="items.precision" size="small" placeholder="0为整数,1为取一位小数..." style="width:100px" clearable />
					</FormItem>
					<FormItem label="最大值" v-if="items.type=='ipt_number'">
						<Input type="number" v-model="items.max" size="small" placeholder="最大值" style="width:100px" clearable />
					</FormItem>
					<FormItem label="最小值" v-if="items.type=='ipt_number'">
						<Input type="number" v-model="items.min" size="small" placeholder="最小值" style="width:100px" clearable />
					</FormItem>
					<!-- rate（评分） -->
					<FormItem label="是否可选半星" v-if="items.type=='rate'">
						<Select v-model="items.allowHalf" size="small" placeholder="是否可选半星" style="width:100px" clearable>
							<Option value="1">是</Option>
							<Option value="0">否</Option>
						</Select>
					</FormItem>
					<!-- 搜索（点击弹出框） -->
					<FormItem label="弹出框类型" v-if="items.type=='search'">
						<Select v-model="items.modelType" size="small" placeholder style="width:100px" clearable>
							<Option value="1">通用树</Option>
							<Option value="2">其他</Option>
						</Select>
					</FormItem>
					<!-- 日期选则器 -->
					<FormItem label="选择器类型" v-if="items.type=='data'">
						<Select v-model="items.timeType" size="small" placeholder="date、datetime" style="width:160px" clearable @on-change="dataChange(items.timeType,index)">
							<Option value="date">yyyy-MM-dd</Option>
							<Option value="datetime">yyyy-MM-dd HH:mm:ss</Option>
						</Select>
					</FormItem>

					<div v-show="items.isMore=='1'">
						<FormItem label="非空验证提示" v-if="items.type!='switch'&&items.type!='rate'">
							<Input v-model="items.message" size="small" placeholder="非空验证报错文本" style="width:150px" clearable />
						</FormItem>
						<FormItem label="提示信息" v-if="items.type!='radio'&&items.type!='checkbox'&&items.type!='switch'&&items.type!='rate'">
							<Input v-model="items.placeholder" size="small" placeholder="请输入提示信息" style="width:150px" clearable />
						</FormItem>
						<FormItem label="是否只读">
							<Select v-model="items.isReadonly" size="small" placeholder="是否可输入" style="width:100px" clearable>
								<Option value="1">是</Option>
								<Option value="0">否</Option>
							</Select>
						</FormItem>
						<FormItem label="是否禁用">
							<Select v-model="items.disabled" size="small" placeholder="是否禁用" style="width:100px" clearable>
								<Option value="1">是</Option>
								<Option value="0">否</Option>
							</Select>
						</FormItem>
					</div>
					<!--<a v-show="items.isMore=='0'" @click="items.isMore='1'" style="position: relative;top:5px;margin-left: 5px;text-decoration: underline;">更多设置</a>-->
					<!-- 下拉框 -->
					<div class="whereArr" v-if="items.type=='select'">
						<p class="clear"></p>
						<section v-for="(item, index_mini) in items.selectArr" :key="index_mini">
							<FormItem label="下拉值">
								<Input v-model="item.val" size="small" style="width:100px" clearable/>
							</FormItem>
							<FormItem label="下拉显示文本">
								<Input v-model="item.text" size="small" placeholder clearable/>
							</FormItem>
							<Icon type="ios-remove-circle-outline" class="delSel" size="24" v-if="index_mini!='0'" @click="delBtn('select',index,index_mini)" />
							<p class="clear"></p>
						</section>
						<Button type="primary" size="small" @click="addBtn('select',index)" class="addSel">新增</Button>
					</div>
					<!-- 级联选择器 -->
					<div class="whereArr" v-if="items.type=='cascader'">
						<p class="clear"></p>
						<section v-for="(item, index_mini) in items.cascaderData" :key="index_mini">
							<FormItem label="级联绑定值">
								<Input v-model="item.value" size="small" style="width:100px" clearable/>
							</FormItem>
							<FormItem label="级联显示文本">
								<Input v-model="item.label" size="small" placeholder clearable/>
							</FormItem>
							<Icon type="ios-remove-circle-outline" class="delSel" size="24" v-if="index_mini!='0'" @click="delBtn('cascader',index,index_mini)" />
							<Button type="primary" size="small" @click="addBtn('cascader',index)" class="addCascader" v-if="index_mini===items.cascaderData.length-1">新增</Button>
							<Button type="primary" size="small" @click="addchild(index,index_mini)" class="addCascader">新增子级</Button>
							<section v-for="(item_child, index_mini_child) in item.children" :key="index_mini_child" style="clear: left;padding-left: 20px;">
								<FormItem label="子级绑定值">
									<Input v-model="item_child.value" size="small" style="width:100px" clearable/>
								</FormItem>
								<FormItem label="子级显示文本">
									<Input v-model="item_child.label" size="small" placeholder clearable/>
								</FormItem>
								<Icon type="ios-remove-circle-outline" class="delSel" size="24" @click="delchild(index,index_mini,index_mini_child)" />
							</section>
							<p class="clear"></p>
						</section>
					</div>
					<!-- radio -->
					<div class="whereArr" v-if="items.type=='radio'">
						<p class="clear"></p>
						<section v-for="(item, index_mini) in items.radioArr" :key="index_mini">
							<FormItem label="单选值">
								<Input v-model="item.val" size="small" style="width:100px" clearable/>
							</FormItem>
							<FormItem label="显示文本">
								<Input v-model="item.text" size="small" placeholder clearable/>
							</FormItem>
							<Icon type="ios-remove-circle-outline" class="delSel" size="24" v-if="index_mini!='0'" @click="delBtn('radio',index,index_mini)" />
							<p class="clear"></p>
						</section>
						<Button type="primary" size="small" @click="addBtn('radio',index)" class="addSel">新增</Button>
					</div>
					<!-- checkbox -->
					<div class="whereArr" v-if="items.type=='checkbox'">
						<p class="clear"></p>
						<section v-for="(item, index_mini) in items.checkboxArr" :key="index_mini">
							<FormItem label="单选值">
								<Input v-model="item.val" size="small" style="width:100px" clearable/>
							</FormItem>
							<FormItem label="显示文本">
								<Input v-model="item.text" size="small" placeholder clearable/>
							</FormItem>
							<Icon type="ios-remove-circle-outline" class="delSel" size="24" v-if="index_mini!='0'" @click="delBtn('checkbox',index,index_mini)" />
							<p class="clear"></p>
						</section>
						<Button type="primary" size="small" @click="addBtn('checkbox',index)" class="addSel">新增</Button>
					</div>
					<!-- autoComplete -->
					<div class="whereArr" v-if="items.type=='autoComplete'">
						<p class="clear"></p>
						<FormItem label="后缀" v-for="(item, index_mini) in items.autoArray" :key="index_mini">
							<Input v-model="item.val" size="small" style="width:100px" clearable/>
							<Icon type="ios-remove-circle-outline" class="delSelSingle" size="24" v-if="index_mini!='0'" @click="delBtn('autoComplete',index,index_mini)" />
						</FormItem>
						<Button type="primary" size="small" @click="addBtn('autoComplete',index)" class="addSelSingle">新增</Button>
						<p class="clear"></p>
					</div>
					<!-- switch -->
					<div class="whereArr" v-if="items.type=='switch'">
						<p class="clear"></p>
						<FormItem label="显示值" v-for="(item, index_mini) in items.switchArr" :key="index_mini">
							<Input v-model="item.val" size="small" style="width:100px" clearable/>
						</FormItem>
						<p class="clear"></p>
					</div>
				</div>
				<p class="clear"></p>
			</Form>
		</div>
	</div>
</template>
<script>
	import { deepCopy } from "@/util/util.js";
	export default {
		name: "fromConfig",
		data() {
			return {
				fromModelData: [],
				isUpdated: false,
			}
		},
		methods: {
			//重新排序
			changeSort(key) {
				this.fromModelData.sort(function(item1, item2) {
					let value1 = item1.sort * 1;
					let value2 = item2.sort * 1;
					if(value1 < value2) {
						return -1;
					} else if(value1 > value2) {
						return 1;
					} else {
						return -1;
					}
				})
				this.getSort(key);
			},
			//排序写入
			getSort(key) {
				let add_index = null;
				this.fromModelData.forEach((item, item_index) => {
					if(item.key == key) {
						add_index = item_index;
					}
					item.sort = item_index + 1;
				});
				$(".form-list").removeClass("add");
				$(".form-list").eq(add_index).addClass("add");
			},
			delFromBtn(index) {
				this.$store.commit('changeColums', {
					"key": this.fromModelData[index].key,
					"setName": "isForm"
				});
				this.fromModelData.splice(index, 1);
			},
			setFromModelData() {
				this.isUpdated = true;
				let columsAll = deepCopy(this.$store.state.colums, []);
				for(let i = 0; i < columsAll.length; i++) {
					if(columsAll[i].isForm) {
						let isExist = false;
						for(let ii = 0; ii < this.fromModelData.length; ii++) {
							if(this.fromModelData[ii].key == columsAll[i].key) {
								isExist = true;
								break;
							}
						}
						if(!isExist) {
							this.addFromModelData(deepCopy(columsAll[i], {}));
						}
					} else {
						let isExist = false;
						let ii = 0;
						for(; ii < this.fromModelData.length; ii++) {
							if(this.fromModelData[ii].key == columsAll[i].key) {
								isExist = true;
								break;
							}
						}
						if(isExist) {
							this.fromModelData.splice(ii, 1);
						}
					}
				}
				this.fromModelData.forEach(function(item, index) {
					item.sort = index + 1;
				});
			},
			addFromModelData(colum) {
				this.fromModelData.push({
					type: "input",
					placeholder: "请输入" + colum.headerName,
					message: colum.headerName+"不能为空",
					isReadonly: "0",
					disabled: "0",
					col: "24",
					fromType: "text",
					rows: null,
					max: null,
					min: null,
					precision: 0,
					allowHalf: "0",
					modelType: "1",
					timeType: "date",
					selectArr: [],
					cascaderData: [],
					radioArr: [],
					checkboxArr: [],
					autoArray: [],
					switchArr: [],
					key: colum.key,
					defaultValue: "",
					label: colum.headerName,
					isMore: "0",
					sort: 0
				});
			},
			addchild(index, index_mini) {
				this.fromModelData[index].cascaderData[index_mini].children.push({
					value: "",
					label: ""
				});
			},
			addBtn(type, index) {
				switch(type) {
					case "select":
						this.fromModelData[index].selectArr.push({
							val: "",
							text: ""
						});
						break;
					case "cascader":
						this.fromModelData[index].cascaderData.push({
							value: "",
							label: "",
							children: []
						});
						break;
					case "radio":
						this.fromModelData[index].radioArr.push({
							val: "",
							text: ""
						});
						break;
					case "checkbox":
						this.fromModelData[index].checkboxArr.push({
							val: "",
							text: ""
						});
						break;
					case "autoComplete":
						this.fromModelData[index].autoArray.push({
							val: ""
						});
						break;
				}
			},
			delchild(index, index_mini, index_mini_child) {
				this.fromModelData[index].cascaderData[index_mini].children.splice(
					index_mini_child,
					1
				);
			},
			delBtn(type, index, index_mini) {
				switch(type) {
					case "select":
						this.fromModelData[index].selectArr.splice(index_mini, 1);
						break;
					case "cascader":
						this.fromModelData[index].cascaderData.splice(index_mini, 1);
						break;
					case "radio":
						this.fromModelData[index].radioArr.splice(index_mini, 1);
						break;
					case "checkbox":
						this.fromModelData[index].checkboxArr.splice(index_mini, 1);
						break;
					case "autoComplete":
						this.fromModelData[index].autoArray.splice(index_mini, 1);
						break;
				}
			},
			//选则表单类型事件
			whereChange(index) {
				let obj = this.fromModelData[index];
				switch(obj.type) {
					case "input":
						obj.width = 100;
						obj.fromType = "text";
						break;
					case "ipt_number":
						obj.width = 80;
						obj.precision = 0;
						break;
					case "select":
						obj.width = 80;
						obj.selectArr = [{
							val: "",
							text: ""
						}];
						break;
					case "data":
						obj.width = 150;
						obj.timeType = "datetime";
						obj.class = "0";
						break;
					case "search":
						obj.width = 120;
						obj.modelType = "1";
						break;
					case "switch":
						obj.switchArr = [{
								val: "开"
							},
							{
								val: "关"
							}
						];
						break;
					case "rate":
						break;
					case "cascader":
						obj.cascaderData = [{
							value: "",
							label: "",
							children: []
						}];
						break;
					case "radio":
						obj.radioArr = [{
							val: "",
							text: ""
						}];
						break;
					case "checkbox":
						obj.checkboxArr = [{
							val: "",
							text: ""
						}];
						break;
					case "autoComplete":
						obj.autoArray = [{
							val: ""
						}];
						break;
				}
			},
			// 时间选则器时间类型选则事件
			dataChange(timeType, index) {
				if(timeType == "date") {
					this.fromModelData[index].width = 100;
				} else {
					this.fromModelData[index].width = 150;
				}
			},
			updateFromColums() {
				if(this.isUpdated) {
					let fromColums = [];
					this.fromModelData.forEach(item => {
						let obj = {
							type: item.type,
							key: item.key,
							label: item.label,
							col: item.col,
							placeholder: item.placeholder,
							message: item.message
						};
						switch(item.type) {
							case "input":
								obj.fromType = item.fromType;
								if(item.rows != null) {
									obj.rows = item.rows;
								}
								break;
							case "ipt_number":
								obj.precision = item.precision;
								if(item.min != null) {
									obj.min = item.min;
								}
								if(item.max != null) {
									obj.max = item.max;
								}
								break;
							case "rate":
								if(item.allowHalf == "1") {
									obj.allowHalf = true;
								}
								break;
							case "search":
								obj.modelType = item.modelType;
								break;
							case "data":
								obj.timeType = item.timeType;
								obj.format =
									item.timeType == "datetime" ?
									"yyyy-MM-dd HH:mm:ss" :
									"yyyy-MM-dd";
								break;
							case "select":
								obj.selectArr = [];
								item.selectArr.forEach(data => {
									obj.selectArr.push({
										val: data.val,
										text: data.text
									});
								});
								break;
							case "cascader":
								obj.cascaderData = [];
								item.cascaderData.forEach(data => {
									obj.cascaderData.push({
										value: data.value,
										label: data.label,
										children: data.children
									});
								});
								break;
							case "radio":
								obj.radioArr = [];
								item.radioArr.forEach(data => {
									obj.radioArr.push(data.val);
								});
								break;
							case "checkbox":
								obj.checkboxArr = [];
								item.checkboxArr.forEach(data => {
									obj.checkboxArr.push(data.val);
								});
								break;
							case "autoComplete":
								obj.autoArray = [];
								item.autoArray.forEach(data => {
									obj.autoArray.push(data.val);
								});
								break;
							case "switch":
								obj.switchArr = [];
								item.switchArr.forEach(data => {
									obj.switchArr.push(data.val);
								});
								break;
						}
						fromColums.push(obj);
					})
					this.$store.commit("changeFromColums", fromColums);
				}

			}
		}
	}
</script>
<style scoped>
	.fix-top {
		position: fixed;
		z-index: 99;
		width: 100%;
		background-color: white;
	}
	
	.title {
		display: inline-block;
		float: left;
		padding-left: 15px;
	}
	
	.btn-div {
		text-align: right;
		padding-right: 30px;
		display: inline-block;
		float: right;
	}
	
	.delSelSingle {
		cursor: pointer;
		color: #0081ba;
	}
	
	.addSel {
		position: relative;
		top: -30px;
		left: 500px;
	}
	
	.addSelSingle {
		margin-left: 10px;
		position: relative;
		top: 5px;
	}
	
	.delSel {
		margin-left: 5px;
		position: relative;
		top: 5px;
		cursor: pointer;
		color: #0081ba;
	}
	
	.delSel:hover,
	.form-del:hover,
	.delSelSingle:hover {
		color: red;
	}
	
	.addCascader {
		position: relative;
		top: 5px;
		left: 5px;
	}
	
	/*.write-form {
		padding: 10px 0;
		border-top: 1px dashed #333333;
	}*/
/*	/*
	.write-form:first-child {
		border: 0;
	}*/
	
	.ivu-form-item {
		float: left;
		margin-bottom: 3px;
	}
	
	.whereArr {
		margin-left: 50px;
	}
	
	table {
		display: block;
		margin: 10px auto;
		border-collapse: collapse;
		width: 1000px;
	}
	
	table td {
		border: 1px solid black;
		padding: 3px 10px;
	}
	
	.form-del {
		position: absolute;
		top: 5px;
		left: 6px;
		cursor: pointer;
		color: #0081ba;
	}
	
	.del-form {
		position: relative;
	}
	
	.selInterface {
		position: relative;
		left: 555px;
		top: 28px;
	}
	
	.ivu-tag-blue {
		cursor: default;
		width: 100px;
		text-align: center;
	}
	
	.write-form:hover {
		box-shadow: 0 0 5px #0081ba;
	}
	
	.form-list.add {
		box-shadow: 0 0 5px #2A53CD;
	}
</style>


