let all = {

    model(chooseModel, apiData, cloumsObj, tableObj) {
        /*  chooseModel：初始全局配置
         chooseModel:{
             hasTree:0, //是否有左侧导航树
            hasExport:0,//是否有导出模块
             hasAdd:0,//是否有表单编辑模块
             theme:'页面主题'，
             mainKey:'',//主字段--一般为id
         }
         apiData:api配置数据集合
         cloumsObj={
            queryCllums:state.queryCllums,
            tableCllums:state.tableCllums,
            destailCllums:state.destailCllums,
            exportCllums:state.exportCllums,
            fromColums:state.fromColums
        }
        tableObj:{//表格模块样式选择
        hasCheckBox:1,//是否复选框
        hasPage: 1,//是否有分页
        buttonAll: []//右侧操作模块
    }
         */




        // 左侧树模块
        let treeCode, treeMethods = '', treeData = '', treeMounted = '', treeTemplate01 = '', treeTemplate02 = '';
        if (chooseModel.hasTree) {
            treeCode = {
                template01: ` <Row>
				<Col span="4">
				 <Card class="tableCard treeCard" style="margin-top: 0px;">
				   <p slot="title">${chooseModel.theme}结构列表</p>
				   <Tree :data="treeData" children-key="childNodes" :render="renderContent"></Tree>
				 </Card>
				</Col>
				<Col span="20"> `,
                template02: `</Col></Row>`,
                data: ` treeData: [],`,
                mounted: ` this.getTreeData();`,
                methods: `
				// 获取菜单结构列表
				getTreeData() {
				  ${apiData[0].get}(
					'${apiData[0].api}',
					{},
					(state, rspMsg, rspData) => {
					  if (state == "FAIL") {
						this.$Message.error("查询异常！");
						return;
					  }
					  if (state == "ERROR") {
						this.$Message.error(rspMsg);
						return;
					  }
					  if (state == "SUCCESS") {
						this.treeData = rspData;
					  }
					}
				  );
				},
				// 树形导入配置
				renderContent(h, { root, node, data }) {
				  return [
					h("span", [
					  h(
						"span",
						{
						  style: {
							cursor: 'pointer'
						  },
						  on: {
							click: () => {
							  this.selOrgItem(data);
							}
						  }
						},
						data.title
					  )
					])
				  ];
				},
				// 左侧树点击事件
				selOrgItem(data) {
				  console.log("左侧树点击事件");
				},
				`
            }
            treeMethods = treeCode.methods
            treeData = treeCode.data
            treeMounted = treeCode.mounted
            treeTemplate01 = treeCode.template01
            treeTemplate02 = treeCode.template02
        }

        //  查询模块代码配置
        let whereAll = {
            isReset: true,
            msg: "必填项不能为空",
            data: [],
            whereFrom: {}
        }
        if (cloumsObj.queryCllums.length > 0) {
            whereAll.data = cloumsObj.queryCllums
            cloumsObj.queryCllums.forEach(item => {
                whereAll.whereFrom[item.key] = ""
            });
        }







		let fromModelCode = {
			template: ``,
			data: ``,
			methods: ``
		}
		let addTemplate = ``;
		if (chooseModel.hasAdd == '1') {
			let setPageStart = '';
			if (tableObj.hasPage) {
				setPageStart = 'this.pageAll.pageInfo.pageCurrent = 1';
      }
      let menuForm={}
      cloumsObj.fromColums.forEach(item=>{
     
        menuForm[item.key]=""
      })
      // console.log(menuForm)
			fromModelCode.template = `<form-model ref="formChild" :fromModelAlls="fromModelAll" @formKeep="formKeep"></form-model>`
			addTemplate = `<Button type="primary" size="small" @click="addBtn">新增</Button>`;
			fromModelCode.data = `
				fromModelAll:{
					isShow: false,
					isCustomKeep: true,
					title:"${chooseModel.theme}信息",
          fromData: ${JSON.stringify(cloumsObj.fromColums)},
          menuForm: ${JSON.stringify(menuForm)}
				},
				updataId:"",
			`
      fromModelCode.methods = `
      //表单编辑弹窗保存事件
      formKeep(object){
				let operationUrl = "";
				let param = {}
				this.fromModelAll.fromData.forEach(item => {
					param[item.key] = object[item.key]
				})
				if(this.updataId == "") { //新增
					operationUrl = "${apiData[2].api}";
				} else { //修改
					operationUrl = "${apiData[3].api}";
					param.${chooseModel.mainKey} = this.updataId;
				}
				${apiData[2].get}(operationUrl, param, (state, rspMsg, rspData) => {
					if(state == "FAIL") {
						this.$Message.error("保存失败！");
						return;
					}
					if(state == "ERROR") {
						this.$Message.error(rspMsg);
						return;
					}
					if(state == "SUCCESS") {
						this.fromModelAll.isShow = false;
						this.$Message.success("保存成功！");
						this.$refs.formChild.isClear = true
						${setPageStart}		
						this.getTableList();
					}
				});
      },
      //新增按钮点击事件
      addBtn() {
				this.updataId = "";
				this.fromModelAll.isShow = true;
      },
      //右侧修改按钮点击事件
      update(object){
				let param = {};
				${reqCondition?'':'param={pageCurrent:this.pageAll.pageInfo.pageCurrent,pageSize:this.pageAll.pageInfo.pageSize}'}
				param.${chooseModel.mainKey} = object.${chooseModel.mainKey};
				${apiData[5].get}("${apiData[5].api}", param,(state, rspMsg, rspData) => {
					if(state == "FAIL") {
									this.$Message.error("查询失败！");
									return;
								}
								if(state == "ERROR") {
									this.$Message.error(rspMsg);
									return;
								}
								if(state == "SUCCESS") {
									let editData = rspData.list;
									this.fromModelAll.fromData.forEach(item => {
										this.fromModelAll.menuForm[item.key] = editData[item.key]
									})
									this.updataId = object.${chooseModel.mainKey};
									this.fromModelAll.isShow = true;
								}
				})
			},`
		}


        //表格模块配置
        // let tableColumns = cloumsObj.tableCllums;
        let buttonAll = tableObj.buttonAll.length==0? '' : 'buttonAll:' + JSON.stringify(tableObj.buttonAll) + ","
        let pageAll = !tableObj.hasPage ? '' : ' pageAll:{pageInfo: {total: 7,pageCurrent: 1,pageSize: 10,lastPage: 4}},'
        let reqCondition = !tableObj.hasPage ? '' : 'pageCurrent:this.pageAll.pageInfo.pageCurrent,pageSize:this.pageAll.pageInfo.pageSize,'
        if (reqCondition) {
            whereAll.data.forEach(item => {
                reqCondition = reqCondition + `${item.key + ':' + 'this.whereAll.whereFrom.' + item.key + ','}`
            })
        }
        // console.log(tableObj.hasCheckBox)
        let checkBox = !tableObj.hasCheckBox ? '' : `
          {
            headerCheckboxSelection: true,
            checkboxSelection: true,
            pinned: "left",
            width: 25,
            isHide: true //详情页是否隐藏
          },
        `
        let page = `
         {
            headerName: "序号",
            resizable: true,
            cellClass: "col_center",
            isHide: true,
            valueGetter: params => {
              if (this.pageAll) {
                var p_n = this.pageAll.pageInfo.pageCurrent;
                var p_l = this.pageAll.pageInfo.pageSize;
                var num = (p_n - 1) * p_l + params.node.rowIndex;
                return num + 1;
              } else {
                return params.node.rowIndex + 1;
              }
              
            }},
        `
        let tableColumns = checkBox + page;
        cloumsObj.tableCllums.forEach(item => {
            tableColumns = tableColumns + JSON.stringify(item) + ','
        })
        let operationClickTxt = '',
            operationBtnTxt = "";
            // console.log(tableObj.buttonAll)
        if (tableObj.buttonAll.length>0) {
            tableObj.buttonAll.forEach(item => {
                    operationClickTxt = operationClickTxt + ` case "${item.click}":  this.${item.click}(item.data);break;`
                    if (item.name.indexOf('修改') != -1) {
                        operationBtnTxt = operationBtnTxt + ` ${item.click}(data) {this.update(data)},`
                    } else {
                        operationBtnTxt = operationBtnTxt + ` ${item.click}(data) { console.log('${item.name}点击事件')},`
                    }
            })
        }
       console.log(operationClickTxt)
      let  tableMethods=`
        //获取表格数据
        getTableData() {            
            ${apiData[1].get}(
              '${apiData[1].api}',
              {${reqCondition}},
              (state, rspMsg, rspData) => {
               if (state == "FAIL") {
                 this.$Message.error("查询失败！");
                 return;
               }
               if (state == "ERROR") {
                 this.$Message.error(rspMsg);
                 return;
               }
               if (state == "SUCCESS") {
                 ${
                   !tableObj.hasPage? 'this.tableData = rspData;' : `this.tableData = rspData.list; this.pageAll.pageInfo.total = rspData.paginator.totalRecords;
                   this.pageAll.pageInfo.lastPage = rspData.paginator.lastPage;`
                   }
               }
              }
            );
        },
        ${tableObj.buttonAll.length==0? '' : `
        //表格操作按钮点击事件
        operationClick(item) {
          switch (item.name) {
            ${operationClickTxt}
         }
         },`}
         ${operationBtnTxt}
        `
        let tableData = `
            tableData:[${JSON.stringify(whereAll.whereFrom)}],
            detailsAll:{
                modelWidth:520,
                modelTitle:"${chooseModel.theme}详情弹窗",
                skyWidth:80,
                tableRowItem:${JSON.stringify(cloumsObj.destailCllums)},
                config: {
                    url: "${apiData[5].api}", //api接口
                    fn: "${apiData[5].get}", //请求方法
                    reqKey: ["${chooseModel.mainKey}"], //请求配置字段
                    sendData: createParameter({})
                }
            },
            tableColumns:[${tableColumns}],
           ${buttonAll}
           ${pageAll}  
        `



		//导出excel配置
		let hasExportExcel, exportExcelAll, exportExcelCode;
		if (chooseModel.hasExport) {
			hasExportExcel = chooseModel.hasExport? true : false
			exportExcelAll = `{ 
			    isExcelModal: false, //控制弹窗显示隐藏
			    exportWhole: {
			    	//导出全部数据配置
				    get: "${apiData[6].get}", //请求数据方法
				    api: "${apiData[6].api}", //请求路径
				    whereFrom: {}, //查询条件接入
				    templateCode: "模板编码", //模板编码
				    sendData: createParameter({})
		    }}`;

			exportExcelCode = {
				template: `<Button type="primary" size="small" class="padding_btn" @click="exportBtn">导出</Button>  <exportExcelModal :exportExcelAll="exportExcelAll" :tableColumns="excelClumns" :tableData="tableData"></exportExcelModal>`,
                data: ` exportExcelAll: ${exportExcelAll},
                        excelClumns:${JSON.stringify(cloumsObj.exportCllums)},`,
				methods: `
                // excel导出点击
                exportBtn(){
                 this.exportExcelAll.isExcelModal=true
                 this.exportExcelAll.exportWhole.whereFrom=this.whereAll.whereFrom
                }, `
			}
		}
      // console.log(tableObj.buttonAll.length > 0)
        let getTablePage=!tableObj.hasPage?"":` 
        // 分页事件点击事件
        getTablePage() {
          this.getTableData();
        },`
        
        return `
        <template>
           <div class="menu_list">
              ${treeTemplate01}
              <Card class="queryCard" dis-hover> 
                <p slot="title">查询条件</p>
                <queryModel ref="queryChild" :whereAll="whereAll" @query="query"  @unfold="unfold"></queryModel>
              </Card>
              <Card class="tableCard" dis-hover>
                <p slot="title">${chooseModel.theme}列表</p>
                <div class="table-btn">
                ${addTemplate}
                ${hasExportExcel ? exportExcelCode.template : ''}
                 
                </div>
                <agGridTable
                  :tableData="tableData"
                  :tableColumns="tableColumns"
                  :detailsAlls="detailsAll"
                  :width="$store.state.tableWidth"
                  ${tableObj.hasPage ? ':pageAll="pageAll"' : ''}
                  ${tableObj.hasPage ? '@getTablePage="getTablePage"' : ''}
                  ${tableObj.buttonAll.length > 0 ? '@operationClick="operationClick"' : ''}
                  :height="tableHeight-38"
                 ></agGridTable>
               </Card>
              ${treeTemplate02}
               ${fromModelCode.template}
           </div>
        </template>
    <script>
      import { serverAjaxReq,clientAjaxReq,createParameter } from "@/util/axios.js";
      import { tableHeightMixin,pageMixin} from '@/util/mixin'
      let _this;
      export default {
        name:'',
        mixins:[tableHeightMixin,pageMixin],
        data() {
          return {
            whereAll:${JSON.stringify(whereAll)},
            ${treeData}
            ${tableData}
            ${hasExportExcel ? exportExcelCode.data : ''}
            ${fromModelCode.data}
          };
        },
        methods: {
          //查询模块点击查询事件
          query(item){
            console.log("查询模块点击查询事件");
            ${tableObj.hasPage?'this.pageAll.pageInfo.pageCurrent = 1;':''}
            this.getTableData()
          },
          ${getTablePage}
          ${treeMethods}
          ${tableMethods}
          ${hasExportExcel ? exportExcelCode.methods : ''}
          ${fromModelCode.methods}
        },
      
        mounted() {
          ${treeMounted}
          this.getTableData()
          ${tableObj.buttonAll.length == 0? '' : ` this.$store.commit("getButtonAll", this.buttonAll);`}
          _this=this;
        },
        watch: {
            ${tableObj.buttonAll.length  == 0? '' : ` $route(to, from) {
                if (to.path == "当前页面router路径") {
                  this.$store.commit("getButtonAll", this.buttonAll);
                }
              }`} 
        },
      };
    </script>
        
        
        
        
        
        
        
        
        
        
        
        
        
        `
    }
}

export default all


