<template>
  <div class="hello">
    <!-- <p>当前正在进行第 {{ current + 1 }} 步</p> -->

    <div class="Steps_wrap">
      <Steps class="Steps_wrap" :current="current" direction="vertical">
        <Step title="模板选定" num="0"></Step>
        <Step title="列表配置" num="1"></Step>
        <Step title="api配置" num="2"></Step>
      </Steps>
      <!--<div style="flex-direction:column;">
			<Icon type="ios-arrow-up" />
			<Icon type="ios-arrow-down"/>
      </div>-->

      <!--<Button type="primary" size="small" @click="previous" :disabled="disabledPrevious">上一步</Button>
      <Button type="primary" size="small" @click="next" :disabled="disabledNext">下一步</Button>-->
    </div>
    <div>
      <!-- 模板选定 -->
      <div class="step-item" v-show="current==0">
        <chooseModel @chooseModelOk="chooseModelOk"></chooseModel>
      </div>
      <!-- 列表配置 -->
      <div class="step-item box" v-show="current==1">
        <filedConfig ref="filedConfig"></filedConfig>
      </div>
      <!-- api配置 -->
      <div class="step-item box" v-show="current==2">
        <apiConfig ref="apiConfig"></apiConfig>
      </div>
      <!-- <div class="step-button" v-show="current!=0">
        <Button type="primary" size="small" @click="previous" :disabled="disabledPrevious">上一步</Button>
        <Button type="primary" size="small" @click="next" :disabled="disabledNext">{{nextName}}</Button>
      </div>-->
      <Drawer width="40" title="生成代码" :closable="false" v-model="value1">
        <textarea id="anyInput" class="left_txt" v-model="$store.state.tarea"></textarea>
        <p class="copy_txt" @click="copy">点击复制</p>
      </Drawer>
      <p class="add_code" @click="generate">点击生产代码</p>
    </div>
  </div>
</template>

<script>
import chooseModel from "./find/chooseModel";
import apiConfig from "./find/apiConfig";
import filedConfig from "./find/filedConfig";
let _this;
export default {
  name: "home",
  components: {
    chooseModel,
    apiConfig,
    filedConfig
  },
  data() {
    return {
      current: 0,
      nextName: "下一步",
      disabledPrevious: true,
      disabledNext: false,
      value1: false
    };
  },
  methods: {
    //复制代码事件
    copy() {
      var target = document.getElementById("anyInput");
      target.select(); // 选择文本
      document.execCommand("Copy"); // 执行浏览器复制命令
    //   alert("已复制好，可贴粘。");
    },
    //点击生产代码事件
    generate() {
      this.value1 = true;
      this.$refs["filedConfig"].dataChange();
      this.$store.commit("generateCode");
    },
    //上一步
    previous() {
      this.disabledNext = false;
      if (this.current > 0) {
        if (this.current == 1) {
          this.disabledPrevious = true;
        }
        this.current--;
      }
    },
    //下一步
    next() {
      this.disabledPrevious = false;
      if (this.current < 2) {
        if (this.current == 1) {
          this.disabledNext = true;
        }
        this.current++;
      }
    },
    //模板选定事件an
    chooseModelOk() {
      // console.log(1)
      this.current = 1;
      this.disabledPrevious = false;
    }
  },
  mounted() {
    _this = this;
    this.$nextTick(() => {
      $(".Steps_wrap .ivu-steps-main .ivu-steps-title").click(function() {
        if (_this.current != 0) {
          let index = parseFloat(
            $(this)
              .parents(".ivu-steps-item")
              .attr("num")
          );
          _this.current = index;
        }
      });
    });
  },
  watch: {
    current(val) {
      if (val == 2) {
        this.$refs["apiConfig"].changeApiData();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello .box {
  width: 1000px;
  margin: 10px auto 0;
  box-shadow: 0 0 2px #333333;
  padding: 0 10px;
  position: relative;
  height: calc(100vh - 20px);
}

.ivu-steps {
  margin-top: 10px;
  width: 98%;
  margin-left: 1%;
}

.step-button {
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.copy_txt{
	position: absolute;
	top: 17px;
	right: 40px;
	color: #0081ba;
	cursor: pointer;
}
.add_code {
  width: 20px;
  font-size: 16px;
  color: #0081ba;
  line-height: 20px;
  position: fixed;
  right: 50px;
  top: 50%;
  margin-top: -70px;
  cursor: pointer;
  text-shadow: 0 0 1px #0081ba;
}

.left_txt {
  width: 100%;
  height: calc(100% - 5px);
}
</style>


