<template>
  <div>
    <section>
      <span class='title-write'>模块标题</span>
      <Input class="theme" @on-focus="focus" v-model="theme" placeholder="请输入模块名称" style="width:600px" clearable/>
      <Row>
        <i-col span="8">
          <figure @click="chooseModel(1,0,0)">
            <img src="@/../static/images/1.png" alt />
            <figcaption>带左侧导航</figcaption>
          </figure>
        </i-col>
        <i-col span="8">
          <figure @click="chooseModel(1,1,0)">
            <img src="@/../static/images/2.png" alt />
            <figcaption>带左侧导航+导出模块</figcaption>
          </figure>
        </i-col>
        <i-col span="8">
          <figure @click="chooseModel(1,1,1)">
            <img src="@/../static/images/3.png" alt />
            <figcaption>带左侧导航+导出模块+表单编辑模块</figcaption>
          </figure>
        </i-col>
      </Row>
      <Row>
        <i-col span="8">
          <figure @click="chooseModel(0,0,0)">
            <img src="@/../static/images/4.png" alt />
            <figcaption>不带左侧导航</figcaption>
          </figure>
        </i-col>
        <i-col span="8">
          <figure @click="chooseModel(0,1,0)">
            <img src="@/../static/images/5.png" alt />
            <figcaption>不带左侧导航+导出模块</figcaption>
          </figure>
        </i-col>
        <i-col span="8">
          <figure @click="chooseModel(0,1,1)">
            <img src="@/../static/images/6.png" alt />
            <figcaption>不带左侧导航+导出模块+表单编辑模块</figcaption>
          </figure>
        </i-col>
      </Row>
    </section>
  </div>
</template>

<script >
export default {
  data() {
    return {
      theme: "" //主题
    };
  },
  methods: {
    //输入页面主题框获取焦点事件
    focus(){
       $('.theme').removeClass('add')
    },
    chooseModel(hasTree, hasExport, hasAdd) {
      if (this.theme) {
        let chooseModel = {
          hasTree: hasTree, //是否有左侧导航树
          hasExport: hasExport, //是否有导出模块
          hasAdd: hasAdd, //是否有新增模块
          theme: this.theme
        };
        this.$store.commit("chooseModelFn", chooseModel);
        this.$emit("chooseModelOk");
      }else{
        this.$Message.error("请先输入模块名称");
        $('.theme').addClass('add')
      }
    }
  }
};
</script>

<style lang="postcss" scoped>
.title-write{
	font-size: 16px;
	font-weight: bold;
	position: absolute;
	top: 5px;
	left: 127px;
}
section {
	position: relative;
  display: block;
  width: 1000px;
  margin: 0 auto;
}
.ivu-row {
  margin: 20px 0;
}
figure {
  display: block;
  width: 95%;
  margin: 0 auto;
  text-align: center;
  padding: 3px;
  border: 1px solid #999999;
  cursor: pointer;
  /* background: red; */
}
figure:hover {
  background: #0081ba;
}
figure:hover figcaption {
  color: white;
}
figure img {
  width: 100%;
  height: 180px;
}
figcaption {
  font-size: 14px;
}
.theme {
  display: block;
  margin: 10px auto 0;
}

</style>



