<template>
    <div class="api">
        <h1 class="title">api配置</h1>
      <Form
      v-for="(items,index) in apiData"
      :key="index"
      :model="items"
      :label-width="170"
      class="write-form" 
      v-show="items.isShow"
    >
      <div class="model_box" style="marginLeft:100px">
        <FormItem :label="items.label"  class="org-input up-org">
              <i-input v-model="items.api" placeholder="请求数据api地址"></i-input>
        </FormItem>
        <FormItem label="管理/业务端"  class="org-input up-org">
              <Select
              v-model="items.get"
              placeholder=""
              clearable
            >
              <Option value="serverAjaxReq">请求管理端后台数据(serverAjaxReq)</Option>
              <Option value="clientAjaxReq">请求业务端后台数据(clientAjaxReq)</Option>
            </Select>
        </FormItem>
        <p class="clear"></p>
     
      </div>
    </Form>
      <!-- {{apiData}} -->
    </div>
</template>

<script>
export default {
     data(){
       return{
             apiData:[
                 {label:"左侧树数据请求API",api:"",get:'clientAjaxReq',isShow:true},
                 {label:"查询数据请求API",api:"",get:'clientAjaxReq',isShow:true},
                 {label:"新增列表项请求API",api:"",get:'clientAjaxReq',isShow:true},
                 {label:"修改列表项请求API",api:"",get:'clientAjaxReq',isShow:true},
                 {label:"删除列表项请求API",api:"",get:'clientAjaxReq',isShow:false},
                 {label:"获取列表项详情数据请求API",api:"",get:'clientAjaxReq',isShow:true},
                 {label:"导出全部数据请求API",api:"",get:'clientAjaxReq',isShow:true},   
             ]       
         }
     },
     methods: {
       changeApiData(){
         let chooseModel=this.$store.state.chooseModel
         this.apiData[0].isShow=chooseModel.hasTree?true:false;
         this.apiData[2].isShow=chooseModel.hasAdd?true:false;
         this.apiData[3].isShow=chooseModel.hasAdd?true:false;
         this.apiData[6].isShow=chooseModel.hasExport?true:false;
       }
     },
     mounted(){
      //  console.log(123)
         this.$store.commit('changeApiData',this.apiData)
     }
}
</script>

<style scoped>
  .api .title{
      font-size: 22px;
      color: #0081BA;
      font-weight: 500;
      text-align: center;
      margin-bottom: 30px;
  }
</style>



