// vuex
import Vue from 'vue'
import Vuex from 'vuex'
import Code from './code'

Vue.use(Vuex)

const state = {
    chooseModel: {
        hasTree: 0, //是否有左侧导航树
        hasExport: 0,//是否有导出模块
        hasAdd: 0,//是否有表单编辑模块
        theme: '页面主题',
        mainKey:'',//主字段--一般为id
    },
    colums: [], //字段配置列表数据导入
    apiData: [//api配置数据集合
        { label: "左侧树数据请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "查询数据请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "新增列表项请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "修改列表项请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "删除列表项请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "获取列表项详情数据请求API", api: "", get: 'clientAjaxReq', isShow: true },
        { label: "导出全部数据请求API", api: "", get: 'clientAjaxReq', isShow: true },
    ],
    queryCllums:[],///查询模块数据导入
    fromColums:[],//编辑模块数据导入
    tableCllums:[],//表格模块数据导入
    destailCllums:[],//详情模块数据导入
    exportCllums:[],//导出模块数据导入
    tarea: '',//生产代码字符串
    tableObj:{//表格模块样式选择
        hasCheckBox:1,//是否复选框
        hasPage: 1,//是否有分页
        buttonAll: []//右侧操作模块
    }
}
const mutations = {
    //代码生成事件
    generateCode(state) {
        let cloumsObj={
            queryCllums:state.queryCllums,
            tableCllums:state.tableCllums,
            destailCllums:state.destailCllums,
            exportCllums:state.exportCllums,
            fromColums:state.fromColums
        }
        let str = Code.model(state.chooseModel,state.apiData,cloumsObj,state.tableObj)
        state.tarea = str
        // console.log(state.chooseModel)
    },
    //模板选择事件--改变chooseModel
    chooseModelFn(state, item) {
        // state.chooseModel = {
        //     hasTree: item.hasTree, //是否有左侧导航树
        //     hasExport: item.hasExport, //是否有导出模块
        //     hasAdd: item.hasAdd,//是否有新增模块
        //     theme:item.theme
        // }
        state.chooseModel.hasTree=item.hasTree
        state.chooseModel.hasExport=item.hasExport
        state.chooseModel.hasAdd=item.hasAdd
        state.chooseModel.theme=item.theme
        console.log(state.chooseModel)
        // console.log()
    },
    //获取字段配置列表数据
    setColums(state, item) {
        state.colums = item;
    },
    //字段配置数据改变事件
    changeColums(state, item) {
        state.colums.forEach(colum => {
            if (colum.key == item.key) {
                colum[item.setName] = false;
            }
        })
    },
     // api配置改变事件
    changeApiData(state, arr) {
        state.apiData = arr
    },
    // queryCllums改变事件
    changeQueryCllums(state,data){
        state.queryCllums=data
        // console.log(data)
    },
    // tableCllums改变事件
    changeTableCllums(state,data){
        state.tableCllums=data
        // console.log(data)
    },
     // destailCllums改变事件
    changeDestailCllums(state,data){
        state.destailCllums=data
    },
     // exportCllums改变事件
    changeExportCllums(state,data){
        state.exportCllums=data
    },
    // fromColums改变事件
    changeFromColums(state,data){
        state.fromColums=data
    },
    //ButtonAll 改变事件
    changeButtonAll(state,data){
        state.tableObj.buttonAll=data
    }
}

export default new Vuex.Store({
    state,
    mutations
})


