<template>
  <div class="query">
    <div id="tuodong" class="where_ipt asddf model_box" style="padding-top: 5px;">
      <section
        class="sec_list"
        v-for="(items,index) in queryClums"
        :key="index"
        @mouseover="mouseover(index)"
      >
        <Form :model="items" :label-width="65" class="write-form">
          <div class="model_box">
            <Icon
              type="ios-remove-circle-outline"
              class="delSel form_btn_remove"
              size="24"
              @click="itemRemove(index)"
            />
            <!-- <FormItem label="排序">
            <InputNumber
              v-model="items.sort"
              size="small"
              style="width:60px !important"
              clearable
            ></InputNumber>
            </FormItem>-->
            <!-- {{items}} -->
            <FormItem label="排序" style="width:100px">
              <InputNumber
                v-model="items.sort"
                size="small"
                :min="1"
                :max="100"
                @on-blur="changeSort(items.sort,index)"
              ></InputNumber>
            </FormItem>
            <!-- <span class="num">{{index+1}}</span> -->
            <FormItem label="表单类型" class="from_items">
              <Select
                v-model="items.type"
                size="small"
                placeholder="请选择表单类型"
                @on-change="whereChange(index)"
                style="width:100px"
                clearable
              >
                <Option value="input">input框</Option>
                <Option value="ipt_number">数字搜索框</Option>
                <Option value="data">日期选择器</Option>
                <Option value="select">下拉选择框</Option>
                <Option value="search">搜索(弹框)</Option>
              </Select>
            </FormItem>
            <FormItem label="查询字段">
              <Input
                v-model="items.key"
                size="small"
                placeholder="查询字段"
                style="width:100px"
                class="form_zd"
                readonly
              />
            </FormItem>
            <FormItem label="提示信息">
              <Input
                v-model="items.headerName"
                size="small"
                placeholder="请输入表单提示信息"
                style="width:150px"
                clearable
              />
            </FormItem>
            <!-- input框 -->
            <!-- <FormItem label="input框类型" v-if="items.type=='input'&&items.fromType">
            <Input
              v-model="items.fromType"
              size="small"
              placeholder="text,password..."
              style="width:100px"
              clearable
            />
            </FormItem>-->
            <!-- 数字框 -->
            <!-- <FormItem label="精度" v-if="items.type=='ipt_number'" style="width:170px">
              <InputNumber
                v-model="items.precision"
                size="small"
                :min="0"
                :max="5"
                placeholder="0为整数,1为取一位小数..."
              ></InputNumber>
            </FormItem>-->
            <!-- 下拉框 -->
            <div class="whereArr" v-if="items.type=='select'">
              <p class="clear"></p>
              <section v-for="(item,num) in items.arr" :key="num" style="border:0">
                <FormItem label="下拉值">
                  <Input v-model="item.objVal" size="small" style="width:100px" clearable />
                </FormItem>
                <FormItem label="显示文本">
                  <Input v-model="item.text" size="small" placeholder clearable />
                </FormItem>
                <Icon
                  type="ios-remove-circle-outline"
                  class="delSel"
                  size="24"
                  v-if="items.arr.length!=1"
                  @click="colRemove(index,num)"
                />
                <!-- <Button type="error"  size="small" class="form_btn"  @click="colRemove(index,num)">-</Button> -->
                <Button type="primary" size="small" class="form_btn" @click="addBtn(index)">+</Button>
                <p class="clear"></p>
              </section>
              <!-- <Button type="primary" size="small" @click="addBtn(items,index)">新增下拉项</Button> -->
            </div>
            <!-- 日期选则器 -->
            <FormItem label="date类型" v-if="items.type=='data'">
              <Select
                v-model="items.timeType"
                size="small"
                placeholder="date、datetime"
                style="width:160px"
                clearable
                @on-change="dataChange(items.timeType,index)"
              >
                <Option value="date">yyyy-MM-dd</Option>
                <Option value="datetime">yyyy-MM-dd HH:mm:ss</Option>
              </Select>
            </FormItem>
            <!-- <FormItem label="结束" v-if="items.type=='data'">
              <Select
                v-model="items.class"
                size="small"
                placeholder="是否为结束时间"
                style="width:30px"
                clearable
              >
                <Option value="0">否</Option>
                <Option value="1">是</Option>
              </Select>
            </FormItem> -->
            <!-- 搜索（点击弹出框） -->
            <!-- <FormItem label="弹出框类型" v-if="items.type=='search'">
              <Select
                v-model="items.modelType"
                size="small"
                placeholder
                style="width:100px"
                clearable
              >
                <Option value="1">通用树</Option>
                <Option value="2">其他</Option>
              </Select>
            </FormItem>-->
            <!-- <Button type="error" class="form_btn_remove" size="small" @click="colRemove(index)">删除</Button> -->
          </div>
          <p class="clear"></p>
        </Form>
      </section>
    </div>
  </div>
</template>
<script>
// import config from "@/../static/config.json";
import { deepCopy } from "@/util/util.js";
export default {
  data() {
    return {
      queryClums: []
    };
  },
  methods: {
    //选则表单类型事件
    whereChange(index) {
      // console.log(index);
      let obj = this.queryClums[index];
      // obj = {
      //   arr: [],
      //   headerName: "",
      //   type: "",
      //   key: ""
      // };
      //  obj.arr=[];
      // this.types = obj.type;
      switch (obj.type) {
        case "input":
          obj.width = 100;
          obj.fromType = "text";
          break;
        case "ipt_number":
          obj.width = 80;
          obj.precision = 0;
          break;
        case "select":
          obj.width = 150;
          obj.arr = [
            { objVal: "1", text: "启用" },
            { objVal: "0", text: "停用" }
          ];
          break;
        case "data":
          obj.width = 150;
          obj.timeType = "datetime";
          obj.class = "0";
          break;
        case "search":
          obj.width = 120;
          obj.modelType = "1";
      }
    },
    // 时间选则器时间类型选则事件
    dataChange(timeType, index) {
      if (timeType == "date") {
        this.queryClums[index].width = 100;
      } else {
        this.queryClums[index].width = 150;
      }
    },
    //删除表单项
    itemRemove(index) {
      this.$store.commit("changeColums", {
        key: this.queryClums[index].key,
        setName: "isSearch"
      });
      this.queryClums.splice(index, 1);
    },
    //减少下拉项
    colRemove(index, num) {
      this.queryClums[index].arr.splice(num, 1);
    },
    // 新增下拉项
    addBtn(index) {
      let obj = { objVal: "", text: "" };
      this.queryClums[index].arr.splice(index + 1, 0, obj);
    },
    //数据获取
    getQueryClums() {
      let data = deepCopy(this.$store.state.colums, []);
      let keys = "";
      this.queryClums.forEach(item => {
        keys += item.key;
      });
      data.forEach(item => {
        if (item.isSearch) {
          if (keys.indexOf(item.key) < 0) {
            item.arr = [];
            item.sort = null;
            this.queryClums.push(item);
          }
        } else {
          this.queryClums.forEach((items, index) => {
            if (items.key == item.key) {
              this.queryClums.splice(index, 1);
            }
          });
        }
      });
      this.getSort();
    },
    //排序写入
    getSort() {
      this.queryClums.forEach((item, index) => {
        item.sort = index + 1;
      });
    },
    //重新排序
    changeSort(num, index) {
      let item = this.queryClums[index];
      this.queryClums.splice(index, 1);
      if (num > this.queryClums.length + 1) {
        num = this.queryClums.length + 1;
      }
      this.queryClums.splice(num - 1, 0, item);
      this.getSort();
      $(".query .sec_list").removeClass("add");
      $(".query .sec_list")
        .eq(num - 1)
        .addClass("add");
    },
    //鼠标移入事件
    mouseover(index) {
      $(".query .sec_list").removeClass("addClass");
      $(".query .sec_list")
        .eq(index)
        .addClass("addClass");
    },
    //生产代码时数据处理
    processingData() {
      let data = [];
      if (this.queryClums.length > 0) {
        this.queryClums.forEach((item,index) => {
          let obj = {
            type: item.type,
            key: item.key,
            placeholder: item.headerName
          };
          switch (item.type) {
            case "input":
               obj.fromType = "text";
              break;
            case "ipt_number":
              obj.precision=0;
              break;
            case "select":
              obj.arr = [];
              item.arr.forEach(data => {
                obj.arr.push({
                  objVal: data.objVal,
                  text: data.text
                });
              });
              break;
            case "data":
              obj.timeType = item.timeType;
              obj.format =
                item.timeType == "datetime"
                  ? "yyyy-MM-dd HH:mm:ss"
                  : "yyyy-MM-dd";
               if(index>0&&this.queryClums[index-1].type=="data"){
                 obj.class="end_time"
               }
              break;
            case "search":
              obj.modelType ="1";
              break;
          }
          data.push(obj);
        });
        this.$store.commit("changeQueryCllums", data);
      }
    }
  },
  mounted() {}
};
</script>
<style scoped>
.fix-top {
  position: fixed;
  z-index: 99;
  width: 100%;
  background-color: white;
}
.title {
  display: inline-block;
  float: left;
  padding-left: 15px;
}
.btn-div {
  text-align: right;
  padding-right: 30px;
  display: inline-block;
  float: right;
}
.write-form {
  padding: 5px 0;
  position: relative;
  padding-left: 20px;
}

/* .where_ipt > section:first-child {
  border: 0;
} */
.from_items {
  /* position: relative;
  z-index: 999;
  background: white; */
}
.ivu-form-item {
  float: left;
  margin-bottom: 3px;
}
.btn {
  margin-bottom: 20px;
}
.whereArr {
  margin-left: 50px;
  /* position: relative; */
}
.form_btn {
  display: block;
  margin-top: 6px;
  float: left;
  margin-left: 5px;
}
.form_btn_remove.delSel {
  position: absolute;
  top: 13px;
  left: 5px;
  z-index: 9999;
}
table {
  display: block;
  margin: 10px auto;
  border-collapse: collapse;
  width: 1000px;
}

table td {
  border: 1px solid black;
  padding: 3px 10px;
}
.model_box {
  width: 100%;
}
.delSel {
  margin-left: 5px;
  position: relative;
  top: 5px;
  display: block;
  float: left;
  cursor: pointer;
  color: #0081ba;
}
.delSel:hover,
.form-del:hover,
.delSelSingle:hover {
  color: red;
}
section {
  display: block;
  border-bottom: 1px dashed #333333;
}
.query {
  /* width: 690px; */
  /* margin: 0 auto; */
}
/* #tuodong{
  height: calc(100vh - 180px);
  overflow: auto;
} */
</style>




