<template>
  <div class="table">
    <Row>
      <i-col span="8">
        <div class="buttonAll">
          <span>右侧操作按钮：</span>
          <Checkbox v-for="item in buttonAll" :key="item.name" v-model="item.has">{{item.name}}</Checkbox>
        </div>
      </i-col>
      <i-col span="8">
        <Form :model="colsAll" :label-width="117" class="write-form chooseCheck">
          <FormItem label="底部有分页">
            <RadioGroup v-model="$store.state.tableObj.hasPage">
              <Radio :label="1">是</Radio>
              <Radio :label="0">否</Radio>
            </RadioGroup>
          </FormItem>
        </Form>
        <p class="clear"></p>
      </i-col>
      <i-col span="8">
        <Form :model="colsAll" :label-width="117" class="write-form chooseCheck">
          <FormItem label="包含复选框">
            <RadioGroup v-model="$store.state.tableObj.hasCheckBox">
              <Radio :label="1">是</Radio>
              <Radio :label="0">否</Radio>
            </RadioGroup>
          </FormItem>
        </Form>
        <p class="clear"></p>
      </i-col>
    </Row>

    <div class="table_list">
      <Form
        v-for="(items,index) in tableColumnsAll"
        :key="index"
        :model="items"
        :label-width="60"
        class="write-form sec_list"
      >
        <div class="model_box" @mouseover="mouseover(index)">
          <Icon
            type="ios-remove-circle-outline"
            class="delSel"
            size="24"
            v-if="tableColumnsAll.length!=1"
            @click="itemRemove(index)"
          />
          <FormItem label="排序" style="width:100px">
            <InputNumber
              v-model="items.sort"
              size="small"
              :min="1"
              :max="100"
              @on-blur="changeSort(items.sort,index)"
            ></InputNumber>
          </FormItem>
          <FormItem label="列标题">
            <Input v-model="items.headerName" size="small" style="width:100px" clearable />
          </FormItem>
          <FormItem label="字段名">
            <Input v-model="items.key" class="form_zd" size="small" style="width:100px" readonly />
          </FormItem>
          <FormItem label="列宽">
            <InputNumber v-model="items.width" size="small" style="width:60px !important" clearable></InputNumber>
          </FormItem>
          <!-- <FormItem label="详情栅格占比">
          <InputNumber
            v-model="items.col"
            size="small"
            style="width:60px !important"
            clearable
          ></InputNumber>
          </FormItem>-->
        </div>
        <!-- <Button type="primary" size="small" class="form_btn" @click="add(index)">+</Button> -->
        <p class="clear"></p>
      </Form>
    </div>
  </div>
</template>
<script>
import { deepCopy } from "@/util/util.js";
export default {
  data() {
    return {
      tableColumnsAll: [],
      colsAll: {
        //表格列选择加入
        hasCheckBox: 1,
        hasPage: 1
      },
      buttonAll: [
        { type: "primary", click: "insertRow", name: "查看", has: false },
        { type: "primary", click: "updateRow", name: "修改", has: false },
        { type: "error", click: "deleteRow", name: "删除", has: false }
      ]
    };
  },
  methods: {
    //删除列表项
    itemRemove(index) {
      this.$store.commit("changeColums", {
        key: this.tableColumnsAll[index].key,
        setName: "isTable"
      });
      this.tableColumnsAll.splice(index, 1);
    },
    //获取数据事件
    getTableColumnsAll() {
      //   console.log(1);
      let data = deepCopy(this.$store.state.colums, []);
      let keys = "";
      this.tableColumnsAll.forEach(item => {
        keys += item.key;
      });
      data.forEach(item => {
        if (item.isTable) {
          if (keys.indexOf(item.key) < 0) {
            // console.log(item.key)
            item.width = 32+12*item.headerName.length;
            if(item.headerName.indexOf("时间")>-1){
              item.width=132
            }
            //  item.col = 12;
            item.sort = null;
            this.tableColumnsAll.push(item);
          }
        } else {
          this.tableColumnsAll.forEach((items, index) => {
            if (items.key == item.key) {
              this.tableColumnsAll.splice(index, 1);
            }
          });
        }
      });
      this.getSort();
    },
   
    //排序写入
    getSort() {
      this.tableColumnsAll.forEach((item, index) => {
        item.sort = index + 1;
      });
    },
    //重新排序
    changeSort(num, index) {
      var item = this.tableColumnsAll[index];
      this.tableColumnsAll.splice(index, 1);
      if (num > this.tableColumnsAll.length + 1) {
        num = this.tableColumnsAll.length + 1;
      }
      this.tableColumnsAll.splice(num - 1, 0, item);
      this.getSort();
      $(".table .sec_list").removeClass("add");
      $(".table .sec_list")
        .eq(num - 1)
        .addClass("add");
    },
    //鼠标移入事件
    mouseover(index) {
      $(".table .sec_list").removeClass("addClass");
      $(".table .sec_list")
        .eq(index)
        .addClass("addClass");
    },
    //生产代码时数据处理
    processingData() {
      let data = [];
      if (this.tableColumnsAll.length > 0) {
        this.tableColumnsAll.forEach(item => {
          let obj = {
            headerName: item.headerName,
            field: item.key,
            width: item.width,
            cellClass: "col_center",
            resizable: true
          };
          data.push(obj);
        });
        let offon = false;
        let buttonAlls=[];
        this.buttonAll.forEach(item => {
          if (item.has) {
            offon = true;
            buttonAlls.push({
              type: item.type, click:item.click, name:item.name,
            })
          }
        });
          this.$store.commit("changeButtonAll", buttonAlls);
        if (offon) {
          data.push({
            headerName: "操作",
            width: 200,
            cellRendererFramework: "operations",
            pinned: "right",
            // isHide: true
          });
        }
        this.$store.commit("changeTableCllums", data);
      }
    }
  }
};
</script>

<style scoped>
.chooseCheck {
  border: 0 !important;
}
/* .table {
  width: 640px;
  margin: 0 auto;
} */
/* .table_list{
  width: 100%;
  max-height:
} */
</style>



